<template>
  <div>
    <!-- <mu-appbar style="width: 100%;"  class="header backgroundNaviBar">
      <a @click="()=>{
           $router.go(-1)
          }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">超标站点</span>
    </mu-appbar>-->
    <van-nav-bar
      :fixed="true"
      title="超标站点"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="filter-o" slot="right" size="20" /> -->
    </van-nav-bar>
    <div class="back">
      <div class="content border-radius" v-for="(item,index) in itemWarningContent" :key="index">
        <div class="headerTable">
          <div class="table">
            <span>站点名称</span>
            <span>{{item.stationName}}</span>
          </div>
        </div>
        <!-- <div class="tableContent"> -->
        <div class="table">
          <span>地址</span>
          <span>{{item.address}}</span>
        </div>
        <div class="table">
          <span>运维人员</span>
          <span>{{item.operator}}</span>
        </div>
        <div class="table">
          <span>联系电话</span>
          <span style="color:#126cf9" @click="callTo(item.phone)">{{item.phone}}</span>
        </div>
        <div class="flexHeader">
          <div style="flex:1">因子</div>
          <div style="flex:2">监测值</div>
          <div style="flex:1">标准值</div>
        </div>
        <div v-for="(item1,i) in item.excessFactorList" :key="i">
          <div class="flexHeader">
            <div style="flex:1">{{item1.factor}}</div>
            <div style="color:#dc0100;flex:2">{{item1.realConc}} {{item1.factor==='pH'?'':'mg/L'}}</div>
            <div
              style="flex:1"
            >{{item1.factor==='pH'?item1.standard.min+'-'+item1.standard.max:item1.standard}} {{item1.factor==='pH'?'':'mg/L'}}</div>
          </div>
        </div>
        <!-- </div> -->
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { Collapse, CollapseItem, Dialog } from "vant";
Vue.use(Collapse)
  .use(CollapseItem)
  .use(Dialog);
export default {
  data() {
    return {
      activeNames: ["1"],
      itemWarningContent: [],
      panel: ""
    };
  },
  async created() {
    var token = sessionStorage.getItem("token");
    var url = "/station/water-station/excessStations";
    var res = await AppBace.request(url, "GET", {}, token);

    console.log("超标站点", res);
    this.itemWarningContent = res.data.data;
  },
  methods: {
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    callTo(number) {
      Dialog.confirm({
        message: number,
        className: "dialogContent"
      })
        .then(() => {
          // on confirm
          window.location.href = "tel://" + number;
        })
        .catch(() => {
          // on cancel
        });
    }
  }
};
</script>
<style scoped>
.title {
  height: 40px;
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
}
.content {
  background-color: white;
  /* border-radius: 10px; */
  padding: 5px;
  margin-bottom: 10px;
}
.back {
  padding: 10px;
  margin-top: 46px;
}
.headerTable {
  /* border-bottom: 2px #ccc solid; */
}
.tableContent {
  padding: 10px;
}
.table {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 42px;
  border-bottom: 1px rgb(240, 240, 240) solid;
  font-size: 12px;
  padding: 0 10px;
  color: #333333;
}
.contentWarning {
  /* padding: 20px; */
}
.header {
  position: fixed;
  top: 0;
}
.images img {
  width: 10px;
  height: 10px;
}
.flexHeader {
  display: flex;
  flex-direction: row;
  height: 42px;
  align-items: center;
  padding: 0 10px;
  justify-content: space-evenly;
}
.flexHeader div {
  width: 30.33%;
  display: flex;
  justify-content: center;
  font-size: 12px;
}
</style>


